<template>
  <div class="wrapp">
    <p class="welcome">Welcome to Creator</p>
    <div v-show="showButtons"> 
      <button @click="goCreate" class="button-create">Create new form</button><button @click="goLists" class="button-lists">Go to lists</button>   
    </div>
  </div> 
</template>

<script>

export default {
  name: 'home',
  data() {
    return {
     
    }
  },
  methods:{    
    goCreate(){
      this.$router.push( {path:'/create'})
    },
    goLists(){
      this.$router.push( {path:'/lists'})
    }
   },
  computed: {
    showButtons(){
      return this.$route.name == 'Home';
    }
  }
}
</script>

<style lang="scss" scoped>
@import './mixins.scss';
  .button-create{
  font-size: 42px;
  color: #fff;
  height: 120px;
  width: 420px;
  border: none;
  cursor: pointer;
  background-color: $main-red;
  margin-bottom: 30px;
  transition: all 1s;
  box-shadow: -2.5px 5px 10px rgba(0,0,0,0.3);
  margin-left: 40px;
  margin-top: 80px;
  opacity: 0.9; 
}

.button-create:hover{
  background-color: $main-red-active;
  opacity: 1;
}

.button-lists{
  @extend .button-create;
  background-color: $main-dark-grey;
  color: white; 
}

.button-lists:hover{
  background-color: #404040; 
}

.wrapp{
  background: url('/../assets/mainbg.png')  top / cover no-repeat;
  height: 100%;
  width: 100%;
}

.welcome{
  font-size: 80px;
  font-weigth: 600px;
  margin-top: 200px;
}

</style>



