<template>
  <div class="conteiner">
    <p class="caption-lists">Lists of form</p>
    <div v-for="(item, index) in getArr">
      
      <div @click="item.preview = !item.preview" class="cont-items">
        <p class="form-title">{{index+1}}. {{item.title}}</p>
        <p class="dicription">{{!!item.discription ? item.discription : 'Discription is emty'}}</p>
        <p class="total-questions">Total {{item.questions.length}} questions</p>
      </div>
      
      <transition name="preview">
        <div v-show="item['preview']" class="box-preview">
          <button @click="item.preview = !item.preview" class="button-to-lists">BACK</button>
          <CreatePreview :form="item" class="preview"/>
        </div>
      </transition>
    
    </div>
  </div>
</template>

<script>

import CreatePreview from '../components/CreatePreview.vue'

export default {
  name: 'ListsOfForm',
  data () {
    return {
      copyForms: []
    }
  },
  components:{
    CreatePreview
  },
  methods: {
    invertBool(val){
      return val = !val
    }
  },
  computed:{
    getArr(){
      return this.$root.$store.state.forms
    }    
  },
  created: function(){    
    this.$root.$store.dispatch('pullForms')
    this.copyForms = JSON.parse(JSON.stringify(this.$root.$store.state.forms))   
  }
}
</script>

<style lang="scss" scoped>
@import './mixins.scss';
  .conteiner{
    text-align: left;
    width: 800px;
    margin: 0 auto;
    background-color: #fff;
    box-shadow: -2.5px 5px 10px rgba(0,0,0,0.3);
    z-index: 999999;
    position: absolute;
    top: -4%;
    right: 50%;
    transform: translate(50%,50%);
  }

  .caption-lists{
    text-align: center;
    font-size: 30px;
    font-weight: 600;
  }
  .cont-items{
    margin-left: 20px;
    padding: 10px 0px 10px 10px;
  }
  
   .cont-items:hover{
    transition: all 2s;
    transform: scale(1.1) translateX(20px);
    box-shadow: -2.5px 5px 10px rgba(0,0,0,0.3);
    cursor: pointer;
  }

  .form-title{
    font-size: 20px;
    font-weight: 600;
  }

  .dicription{
    font-size: 16px;
    font-style: italic;
    // margin-top: -15px;
  }
  .total-questions{
    font-size: 14px;
    // margin-top: -10px;
  }
  .box-preview{
  width: 100%;
  height: 100%;
  background-color: $main-grey;
  /*opacity: 0.5;*/
  position: absolute;
  left: 0;
  top: 0;
  z-index: 99999999999;
  /*overflow: hidden;*/
  }
  .button-to-lists{
    height: 100%;
    background-color: transparent;
    border: none;
    color: white;
    font-size: 16px;
    font-wieght: 600px;
    transition: all 2s;
    text-align: center;
    width: 80px;
  }
  .button-to-lists:hover{
    background-color: $main-dark-grey;
  }

  .preview-enter-active{
    animation: tran .5s ease;
  }
  .preview-leave-active{
    animation: tran .5s ease reverse;
  }

  @keyframes tran {
  0% {
    transform: translateY(500px);
  } 
  50% {
    transform: translateY(250px);
  }
  100% {
    transform: translateY(0);
  }
}

</style>



